.App{
    max-width: 1340px;
    min-height: 780px;
    margin: 0 auto;
    padding: 50px 60px;
    background-color: #fff;
    border-radius: 10px;
    overflow: hidden;
}
#root{
    background-color: #FFDF8C;
    padding: 100px 0px;
    height: 100%;
}


@media (max-width:600px) {
    .App{
        padding: 30px 10px;
    }
}